@media only screen and (min-width: 950px) {
  .content {
    padding: 0 150px 60px;
    width: 80%;
  }
}

h3 {
  font-family: monospace;
  padding-top: 10px;
  padding-bottom: 5px;
}

.content {
  padding: 0 20px 60px;
  font-family: "Oswald", sans-serif;
}

.project-link {
  font-size: 1.3rem;
  color: #6b8e23;
  font-family: "Acme";
}

.space-above {
  padding-top: 10px;
}

.space-below {
  padding-bottom: 20px;
}

.image {
  border: 5px solid #6b8e23;
  border-radius: 3px;
  opacity: 1;
  width: 150px;
}

.image :hover {
  opacity: 0.5;
}

.one-project {
  width: 90%;
  margin-bottom: 5px;
}

.blockc {
  display: block;
  text-align: left;
  width: 300px;
  margin-top: 8px;
}

.icon {
  padding: 20px;
}

.title {
  display: block;
  text-align: center;
}

.aboutme {
  text-align: center;
}

.footer {
  display: flex;
  justify-content: center;
  background: #fffafa;
  color: #6b8e23;
}

.hero {
  background: #fffafa;
  color: #6b8e23;
  padding: 2px 0 18px;
  border: none;
  margin: none;
}

.nav {
  background: #fffafa;
  background-color: #fffafa;
  color: #6b8e23;
  padding: 10px 10px 0px 10px;
  display: flex;
}

.nav-item {
  color: #6b8e23;
}

.active {
  background-color: #e8f1f2;
}

.button {
  background-color: #6b8e23;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 2px 20px 2px;
  white-space: nowrap;
}

#button {
  background-color: #6b8e23;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 2px 20px 2px;
}

#root {
  background-color: #fffafa;
}

#h2 {
  padding: 70px 0 10px 0;
  white-space: nowrap;
  text-align: left;
  color: #6b8e23;
  font-size: 40px;
  font-family: Acme;
}

#resume {
  margin: 10px 0px;
}
